import { Component, OnInit } from '@angular/core';
import { ProposalListPreviewModule } from '../proposal-list-preview/proposal-list-preview.module';
import { ProposalList } from '../model/proposal-list';
import { Article } from '../model/article';
import { User } from '../model/user';
import { Userstore } from '../services/user-store';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {

  public title = "Most popular locations";
  public title2 = "For you";
  public title3 = "Near your location"

  public openProposalList: ProposalList;
  public openArticle: Article;

  private currentUser: User;

  constructor(
    private userStore: Userstore
  ) { 
    this.currentUser = this.userStore.retrieveCurrentUser();
  }

  ngOnInit(): void {
  }

  public get hasBackButton(): boolean {
    return !!this.openProposalList || !!this.openArticle;
  }

  public onOpenProposalListEvent(list: ProposalList) {
    this.openProposalList = list;
  }

  public onOpenArticleEvent(article: Article) {
    this.openArticle = article;
  }

  public onBack() {
    if (!!this.openArticle) {
      this.openArticle = undefined;
    }
    else if (!!this.openProposalList) {
      this.openProposalList = undefined;
    }
  }

  public get showProposalList(): boolean {
    return !!this.openProposalList && !this.openArticle;
  }

  public get showArticle(): boolean {
    return !!this.openArticle;
  }

  public get articleFavIcon(): string {
    const article = !!this.openArticle && this.currentUser.favorites.find(a => a.id === this.openArticle.id);
    return !!article ? "favorite" : "favorite_border";
  }

  public addToFavs() {
    const art = this.currentUser.favorites.find(a => a.id === this.openArticle.id);
    if (!art) {
      const favs = [
        ...this.currentUser.favorites,
        this.openArticle
      ];
      this.currentUser.favorites = favs;
    }
    else {
      const favs = this.currentUser.favorites.filter(a => a.id !== this.openArticle.id);
      this.currentUser.favorites = favs;
    }
    this.userStore.updateCurrentUser(this.currentUser);
  }

}
