import { Injectable } from '@angular/core';
import { Userstore } from './user-store';
import { User } from '../model/user';
import { Router } from '@angular/router';
import { trimTrailingNulls } from '@angular/compiler/src/render3/view/util';

@Injectable()
export class LoginService {

    constructor(
        private userStore: Userstore
    ) {}

    public login(username: string, password: string): number {
        const users: User[] = this.userStore.retrieveUsers();
        const lUser = users.find(
            u => !u.password.localeCompare(password) 
                && (
                    !u.username.localeCompare(username)
                    ||!u.email.localeCompare(username)
                ) 
        );
        if (!!lUser) {
            this.userStore.setCurrentUser(lUser);
            return 0;
        }
        return -1;
    }

    public logout() {
        this.userStore.removeCurrentUser();
    }
}