import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Feedback } from '../model/feedback';
import { FormGroup, FormBuilder, FormControl } from '@angular/forms';
import { Userstore } from '../services/user-store';

@Component({
  selector: 'app-feedback-dialog',
  templateUrl: './feedback-dialog.component.html',
  styleUrls: ['./feedback-dialog.component.scss']
})
export class FeedbackDialogComponent implements OnInit {

  public comment = new FormControl("");
  public stars: number = 0;
  public healthStars: number = 0;

  constructor(
    private dialogRef: MatDialogRef<FeedbackDialogComponent, Feedback>
    ) {}

  ngOnInit(): void {
  }

  public close() {
    this.dialogRef.close(undefined);
  }

  public send() {
    const feedback: Feedback = {
      articleID: 0,
      author: "",
      stars: this.stars,
      healthStars: this.healthStars,
      comment: this.comment.value
    };
    this.dialogRef.close(feedback);
  }

  public get fullStars(): number[] {
    return Array(this.stars).fill(0).map((_, i) => i + 1);
  }

  public get emptyStars(): number[] {
    const rest = 5 - this.stars;
    return Array(rest).fill(0).map((_,i) => this.stars + i +1);
  }

  public get fullHStars(): number[] {
    return Array(this.healthStars).fill(0).map((_, i) => i + 1);
  }

  public get emptyHStars(): number[] {
    const rest = 5 - this.healthStars;
    return Array(rest).fill(0).map((_,i) => this.healthStars + i +1);
  }

  public setStars(s: number) {
    if (s === this.stars) {
      this.stars = 0;
      return;
    }
    this.stars = s;
  }

  public setHStars(s: number) {
    if (s === this.healthStars) {
      this.healthStars = 0;
      return;
    }
    this.healthStars = s;
  }

}
