import { Component, OnInit } from '@angular/core';
import { FormBuilder, Form, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { RegisterService } from '../services/register.service';
import { User } from '../model/user';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss']
})
export class RegisterComponent implements OnInit {

  public form: FormGroup;
  private error: number = 0;

  constructor(
    private fb: FormBuilder,
    private router: Router,
    private registerService: RegisterService
  ) {
    this.form = this.fb.group({
      username: "",
      email: ["", Validators.email],
      password: "",
      cPassword: ""
    });

    this.form.valueChanges.subscribe(
      _ => this.error = 0
    )
   }

  ngOnInit(): void {
  }

  public onBack() {
    this.router.navigate([""]);
  }

  public get emailError(): string {
    if (this.form.invalid)
      return "Invalid e-mail!";
    if (this.error === -1)
      return "E-mail already in use!";
    return "";
  }

  public get usernameError(): string {
    if (this.error === -2)
      return "Username already in use!";
    return "";
  }

  public get passwordError(): string {
    if (this.form.value.password && this.form.value.password.length < 8)
      return "Use at least 8 characters!"
    return "";
  }

  public get cPasswordError(): string {
    const password = this.form.value.password;
    const cPassword = this.form.value.cPassword;
    if (!!cPassword && cPassword !== password)
      return "Passwords do not match!";
    return "";
  }

  public get inValid(): boolean {
    const pError = this.passwordError;
    const cpError = this.cPasswordError;
    const email = this.form.value.email
    const username = this.form.value.username;
    const password = this.form.value.password;
    const cPass = this.form.value.cPassword;
    return !!pError || !!cpError ||this.form.invalid || !email || !username || !password || !cPass || this.error !== 0;
  }

  public register() {
    const user: User = {
      username: this.form.value.username,
      email: this.form.value.email,
      password: this.form.value.password,
      activities: [],
      locations: [],
      favorites: []
    }
    this.error = this.registerService.register(user);
    if (!this.error)
      this.router.navigate(["main"]);
  }

}
