import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, FormControl } from '@angular/forms';
import { Article, fetchArticles } from '../model/article';

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.scss']
})
export class SearchComponent implements OnInit {

  public searchControl = new FormControl("");
  public articles: Article[] = [];

  public openArticle: Article;

  constructor() { }

  ngOnInit(): void {

    this.searchControl.valueChanges.subscribe(
      value => {
        const searchterms: string[] = value.split(" ");
        this.articles = fetchArticles().filter(art => this.filterArticle(art, searchterms));
      }
    );
  }

  public hideTitle(): boolean {
    return !!this.articles && !!this.articles.length;
  }

  private filterArticle(article: Article, searchterms: string[]): boolean {
    for (let searchterm of searchterms) {
      if (!article.tags.includes(searchterm.toLocaleLowerCase()))
        return false;
    }
    return true;
  }

  public get hasBackButton(): boolean {
    return !!this.openArticle;
  }

  public onBack() {
    if (!!this.openArticle)
      this.openArticle = undefined;
    this.articles = undefined;
  }

  public onOpenArticleEvent(article: Article) {
    this.openArticle = article;
  }
}
