import { Injectable } from '@angular/core';
import { User } from '../model/user';
import { unescapeIdentifier } from '@angular/compiler';

@Injectable()
export class Userstore {

    private usersKey = "users";
    private currentUserKey = "currentUser";

    public retrieveUsers(): User[] {
        const users: User[] = JSON.parse(localStorage.getItem(this.usersKey));
        return users || [];
    } 

    public addUser(user: User) {
        const users = [
            ...this.retrieveUsers(),
            user
        ];
        localStorage.setItem(this.usersKey, JSON.stringify(users));
        localStorage.setItem(this.currentUserKey, JSON.stringify(user));
    }

    public retrieveCurrentUser(): User | undefined {
        const user: User = JSON.parse(localStorage.getItem(this.currentUserKey));
        return user || undefined;
    }

    public setCurrentUser(user: User) {
        localStorage.setItem(this.currentUserKey, JSON.stringify(user));
    }

    public removeCurrentUser() {
        localStorage.removeItem(this.currentUserKey);
    }

    public updateCurrentUser(user: User) {
        const users = [
            ...this.retrieveUsers().filter(u => !!u.username.localeCompare(user.username)),
            user
        ];
        localStorage.setItem(this.usersKey, JSON.stringify(users));
        this.setCurrentUser(user);
    }
}