import { Injectable } from '@angular/core';
import { Feedback } from '../model/feedback';
import { Article, fetchArticles } from '../model/article';

@Injectable()
export class FeedBackStore {

    private feedbackKey = "feedbacks";

    public retrieveFeedbacks(): Feedback[] {
        const feedbacks: Feedback[] = JSON.parse(localStorage.getItem(this.feedbackKey));
        if (!!feedbacks && feedbacks.length)
            return [...this.getDefaultFeedback(), ...feedbacks];
        return [...this.getDefaultFeedback()];
    }

    public retrieveArtFeedbacks(ar: Article): Feedback[] {
        return this.retrieveFeedbacks().filter(f => f.articleID === ar.id);
    }

    public addFeedBack(feedback: Feedback) {
        const feedbacks = [
            ...this.retrieveFeedbacks(),
            feedback
        ];
        localStorage.setItem(this.feedbackKey, JSON.stringify(feedbacks));
    }

    private getDefaultFeedback(): Feedback[] {
        let feeds = [];
        for (const a of fetchArticles()) {
            const f = {
                articleID: a.id,
                author: "stephan",
                stars: 5,
                healthStars: 4,
                comment: "Nice location!",
            };
            feeds = [
                ...feeds,
                f
            ];
        }
        return feeds;
    }

}